// 起始
input[type='radio'] + label{
    display: flex;
    align-items: center;
    width: 100%;
    height: 26px;
    padding: 0 5px 0 36px;
    outline: 0;
    transition: all 0.2s;
    cursor: pointer;
    position: relative;
    color: #ccc;

    &:before{
        height: 25px;
        width: 26px;
        display: inline-block;
        position: absolute;
        top: 2px;
        left: 0;
        background: #434343;
        content: "";
        @include border();	 
    }
    span{
    	position: relative;
    	display: block;
    	top: 3px;
    	width: 100%;
    	
    }
}
input[type='radio']:checked + label{
    // color: #000;
}
//點下
input[type='radio']:checked + label:after {
    width: 10px;
    height: 10px;
    background: #fff;
    display: inline-block;
    position: absolute;
    top: 9px;
    left: 8px;
    content: "";
    @include border();	 
    @include screen(800px){
    	top: 6.5px;
    }
}
