/*----------------------通用設定------------------------*/

@import url(https://fonts.googleapis.com/earlyaccess/cwtexfangsong.css?20190222);

//選取顏色
p,
h1,
h2,
h3,
h4,
b,
strong,
span,
li,
div,
a,
img,
tr,
td,
th {
    &::selection {
        color: $white;
        background: #2f3a88;
    }
}

body {
    position: relative;
    margin: 0 auto;
    width: 100%;
    height: 100%;
    min-height: 100%;
    line-height: 1em;
    overflow: auto;
    font-size: 16px;
    color: $white;
    word-break: break-word;
    letter-spacing: 0.1em;
    font-family: $font_content;
    z-index: 0;
    background: #fff;
    -webkit-text-size-adjust: 100%;
}

//a tag
a {
    text-decoration: none;
    outline: 0;
}

a,
a:hover {
    transition: 0.4s;
}

img {
    vertical-align: middle;
    max-width: 100%;
}

//phone
a[x-apple-data-detectors] {
    color: inherit !important;
    text-decoration: none !important;
    font-size: inherit !important;
    font-family: inherit !important;
    font-weight: inherit !important;
    line-height: inherit !important;
}

a[href^="tel"] {
    color: inherit;
    /* Inherit text color of parent element. */
    text-decoration: none;
    /* Remove underline. */
    /* Additional css `propery: value;` pairs here… */
}

//border-box
*,
*:before,
*:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

//jqimgFill 
.jqimgFill {
    position: relative;
    overflow: hidden;
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100%;
    display: inline-block;
    transition: opacity .4s ease-in-out, transform .4s ease-in-out;
}

.jqimgFill img,
.jqimgFill-tc img {
    left: 0;
    top: 0;
    margin: 0;
    display: block;
    width: auto;
    height: auto;
    min-width: 100%;
    min-height: 100%;
    max-height: none;
    max-width: none;
    transition: opacity .4s ease-in-out, transform .4s ease-in-out;
}

// input,select,textarea
input,
select,
textarea {
    width: 100%;
    height: 56px;
    outline: none;
    padding: 0 10px;
    color: $black;

    border: 1px solid $white;
    font-size: pxToEm(16);
    border-radius: 5px;

    @include screen(640px) {
        font-size: pxToEm(15);
    }

    &:focus {
        transition: .4s;
        border: 1px solid #eee;
    }
}

input:-webkit-autofill,
textarea:-webkit-autofill,
select:-webkit-autofill {
    background: #000 !important;
}

textarea {
    height: 218px;
    padding: 10px;
}

// input placeholder
::-webkit-input-placeholder {
    /* WebKit browsers */
    color: #666;
}

:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #666;
}

::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #666;
}

:-ms-input-placeholder {
    /* Internet Explorer 10+ */
    color: #666;
}

//移除 input type="date"箭頭
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none !important;
    margin: 0;
}

input[type="number"] {
    -moz-appearance: textfield;
}

input[type='date'],
input[type='time'] {
    -webkit-appearance: none;
}

select {
    font-family: $font_content;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    //background: url(../svg/arrw_1.svg?20181225) no-repeat scroll 96.9% center transparent;
    background-size: 3.4%;
    padding-right: 14px;
    height: 40px;
    width: 100%;
}

select::-ms-expand {
    display: none;
}

.selectBox {
    background: #FFF;
}

// header fix狀態時 錨點用
body :target:before {
    display: block;
    content: "";
    height: 120px;
    margin: -120px 0 0;

    @media screen and (max-width: 960px) {
        height: 100px;
        margin: -100px 0 0;
    }
}

body .slide-down :target:before {
    display: block;
    content: "";
    height: 120px;
    margin: -120px 0 0;
}

.slick-slide {
    outline: 0;
}






/*----------------------大區塊------------------------*/

#Wrapper {
    position: relative;
    margin: 0 auto;
    width: 100%;
    height: 100%;
    min-height: 100%;
    min-width: 320px !important;
    overflow: hidden;
    z-index: 2;
}

main[role="main"] {
    display: block;
    width: 100%;
    @extend %cf;
    position: relative;
    margin-top: 175px;
    padding-bottom: 55px;
    z-index: 1;
}

section {
    position: relative;
    @extend %cf;
}

article {
    @include secction_width(1200px, 85%);
    position: relative;
    z-index: 1;
}


/*----------------------每個選單Highlight------------------------*/




/*----------------------內頁單元標題------------------------*/

.title_box {}

/*----------------------tag_list------------------------*/
.tag_list {
    a {
        color: $white;
        text-decoration: none;
        @include border(10px);
        background: #434343;
        padding: 3px 7px;
        font-size: pxToEm(15);

        &:not(:last-child) {
            margin-right: 15px;
        }

        &:hover {
            background: #2f3a88;
        }
    }
}

/*----------------------編輯器------------------------*/

.editor_Content {
    position: relative;
    width: 100%;
    margin: 0 auto;
    padding: 0 0;

    header {
        width: 100%;
        padding-bottom: 31px;
        margin-bottom: 29px;
        border-bottom: solid 1px #7d7d7d;
        display: flex;

        h2 {
            width: 80%;
            font-size: pxToEm(20);
            line-height: 1.4;
            color: $white;
            padding-right: 2%;
        }

        .tag_list {
            text-align: right;
            @include calc(73%);
        }
    }
}

.editor_Box {
    margin: auto;
    line-height: 1.5;
    font-size: pxToEm(15);
    color: $white;

    ul,
    ol {
        padding-left: 40px;
    }

    img {
        max-width: 100%;
        height: auto !important;
    }

    iframe {
        max-width: 100%;

        @media screen and (max-width: 767px) {
            height: auto !important;
        }
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        margin: inherit;
    }
}

.mo_use {
    display: none;
}


/*----------------------back------------------------*/

.back_btn,
.more_btn {
    width: 100%;
    margin-top: 70px;
    font-size: pxToEm(18);

    a {
        margin: auto;
        display: inline-block;
        color: $white;
        text-decoration: none;
        padding: 19.5px 49px;
        background-color: $blue;
        @include border(5px);

        &:hover {
            background: $blue;
            opacity: .5;
        }
    }
}

/*----------------------上下則------------------------*/

.seesaw {
    position: relative;
    width: 100%;
    font-size: pxToEm(16);
    margin-top: 61px;
    display: flex;
    justify-content: center;
    align-items: center;

    a {
        color: $orange;
        text-decoration: none;
    }

    .left_button:before,
    .right_button:before,
    .back_button {
        transition: 0.4s;
    }

    .left_button,
    .right_button {
        position: absolute;

        &:before {
            content: '';
            display: block;
            width: 70px;
            height: 2px;
            background: $orange;
            margin-bottom: 2px;
        }

        &:hover:before {
            width: 90px;
        }
    }

    .left_button {
        right: 0;
    }

    .right_button {
        left: 0;
        text-align: right;
    }

    .back_button {
        font-size: pxToEm(16);
        width: 230px;
        height: auto;
        padding: 11px 0;
        border: 1px solid $orange;
        text-align: center;

        &:hover {
            background: $orange;

            a {
                color: $white;
            }
        }
    }
}


/*----------------------GO TOP------------------------*/

.top_btn {
    cursor: pointer;
    transition: .4s;
    opacity: 0;
    position: fixed;
    bottom: 3%;
    right: 43px;
    z-index: 8;
    font-size: pxToEm(12);
    z-index: 9;
    width: 18px;
    height: 76px;

    a {
        display: block;
        color: $white;
        position: absolute;
        bottom: 0;
        left: 16px;
    }

    &:hover a {
        color: $blue;
    }

    &.active {
        transition: 0s;
        opacity: 1;
    }

    &.fix {
        position: absolute;
        transition: 0s;
        bottom: 112.2%;

        @include screen(768px) {
            bottom: 55.2%;
        }
    }
}


/*----------------------頁數------------------------*/
$lr_btn:17px;

%list_a {
    display: block;
    margin: 0 auto;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

.page {
    font-size: pxToEm(18);
    clear: both;
    width: 100%;
    text-align: center;
    margin: 0 auto;

    dt,
    dd {
        vertical-align: middle;
        display: inline-block;

        a {
            display: block;
            height: 32px;
            line-height: 30px;
            text-decoration: none;
            color: $white;

            &:hover {
                color: #2f3a88;
            }
        }
    }

    dd {
        width: 2.3%;
    }

    dd.active a {
        @include border();
        background: none;
        padding: 0;
        color: $blue;
        font-weight: bold;
    }

    dt.rtbn a,
    dt.ltbn a {
        width: 32px;
        height: 32px;
        transition: 0.4s;
        @include border(99em);
        background: #2b2c2e;

    }

    .nopage {
        opacity: 0.2;
    }

    dt.ltbn a {
        margin-right: $lr_btn;

        &:before {
            @extend %list_a;
            @include triangle_specially(left_l, 7px, 7px, $white);
        }

        &:hover {
            background: #2f3a88;
        }
    }

    dt.rtbn a {
        margin-left: $lr_btn;

        &:before {
            @extend %list_a;
            @include triangle_specially(right_r, 7px, 7px, $white);
        }

        &:hover {
            background: #2f3a88;
        }
    }
}

//版本使用
@include screen(768px) {
    .mo_use {
        display: block;
    }

    .pc_use {
        display: none;
    }

    .top_btn {
        right: 20px;
    }

    right: 20px;
}

@include media-breakpoint-down(lg) {}

@include media-breakpoint-down(md) {}

@include media-breakpoint-down(xs) {

    .top_btn a:hover {}

    .page {
        font-size: pxToEm(17);

        dd {
            width: 11%;
        }

        dt.ltbn a {
            margin-right: pxToEm(14);

            &:before {
                border-width: 5px 5px 5px 0;
            }
        }

        dt.rtbn a {
            margin-left: pxToEm(14);

            &:before {
                border-width: 5px 0 5px 5px;
            }
        }

        dt.ltbn a,
        dt.rtbn a {
            width: 28px;
            height: 28px;
        }
    }

    .back_btn,
    .more_btn {
        font-size: pxToEm(17);

        a {
            padding: 12.5px 25px;
            border-radius: 4px;
        }
    }

}